/*********************************************************************************************

     Hero Viewport

*********************************************************************************************/

.hero{
    width: 100%;
    overflow: hidden;
    position: relative;
}
                         
.cover-container{
    position: relative;
    overflow: hidden;
}

.hero-viewport{
    width: 100%;
    overflow: hidden;
}

.hero-viewport-image{
    width: 100%;
    background: #151515 url("../images/backgrounds/banner01.jpg");
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;
}

.hero-content{
    color: #fff;
    padding: 15em 0;
    width: 100%;
    z-index: 25;
    text-align: center;
}


