.mt-hero-textwrap{
    padding: 0.5em 3em;
    text-align: left;

    .mt-heading-text{
        padding: 0.2em 0;
        font: {
            size: 25px;
            weight: bold;
        }
    }
}                  

.mt-hero-button{
    text-align: left;
    margin: 2em 6em;
}
  .button_09{
    -webkit-transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    background-color: transparent;
    border-radius: 2px;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-weight: 600;
    height: 3em;
    line-height: 3em;
    padding: 0 2em;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
}

.button_09{
    color: #fff;
    min-width: 12em;
    font-size: .95rem;
} 

.special{
       background: linear-gradient(to bottom,#F23054,#F03054);

}

span.special:hover{
    background: linear-gradient(to bottom, #D82849, #DC2749);
}
.mt-slide-button{

    margin: 2em 2em;
}

//==================================================================*
.mt-small-banner{
    height: 55vh;
    background: url('../images/backgrounds/bb2.jpg');
    background-size: cover;
    background-repeat: no-repeat;
}

@media only screen and (max-width: 1024px) and (min-width: 480px){
    .mt-small-banner{
        background-size: contain;
    }

    p.attract-me{
        font-size: 1.14286rem !important;
    }

    .bold-title{
        font-size: 1.7rem !important;
    }
    .mt-content-section-wrapper {
        padding: 0 5em !important;
    }

    .mt-info-box {
        width: 100% !important;
    }
}


@media only screen and (max-width: 480px){
    .mt-content-section-wrapper {
        padding: 0 1em !important;
    }

    .mt-info-box {
        width: 100% !important;
    }

    p.attract-me{
        font-size: 1.14286rem !important;
    }

    .bold-title{
        font-size: 1.5rem !important;
    }

    .mt-wrap-ptb {
        padding: 2em 0 !important;
    }

}